<script>
import {
  getDomainType,
  isSub,
  CheckSearchLegal,
  getDomainTopType,
  CheckLegalRoot,
} from "@/utils/Validator.js";
import VLayout from "@/layouts/Default.vue";
import PageContainer from "@/components/PageContainer.vue";
import HeaderCarousel from "./components/Carousel.vue";
import AboutSection from "./AboutSection.vue";
import MileStoneLine from "./MileStoneLine.vue";
// import Top10DomainCombox from './Top10DomainCombox'
import FavoriteSection from "./FavoriteSection";
import DecenterSection from "./DecenterSection.vue";
import CritSection from "./CritSection.vue";
import FutureSection from "./FutureSection.vue";
import SeeNewsSection from "./SeeNewsSection.vue";
import AppSences from "./AppSences.vue";

import TripleEleCards from "./components/TripleEleCards";

import FootContainer from "@/footer/FootContainer.vue";

export default {
  name: "HomeIndex",

  components: {
    VLayout,
    PageContainer,
    HeaderCarousel,
    AboutSection,
    MileStoneLine,
    // Top10DomainCombox,
    FavoriteSection,
    DecenterSection,
    CritSection,
    SeeNewsSection,
    FutureSection,
    AppSences,
    FootContainer,
    TripleEleCards,
  },
  data() {
    return {
      subSearchText: "",
      topSelectText: "",
      topSearchText: "",
      submodel: {
        defaultSize: 20,
        maxSize: 50,
        filterkey: "",
        popvisible: false,
        loading: false,
        customize: "",
        isCustomizeRoot: false,
      },
      ctrl: {
        tabActived: "sub",
        registState: false,
        searchState: false,
      },
      ruleState: {
        decimals: 18,
        rareBas: 2000,
        rootBas: 200,
        subBas: 4,
        externalBas: 100,
        maxRegYears: 5,
        aliasLen: 256,
        extensionLen: 512,
      },
      tabShow: false,
    };
  },
  computed: {
    subActived() {
      return this.ctrl.tabActived === "sub";
    },
    topArrowClass() {
      return this.submodel.popvisible ? "fa-chevron-up" : "fa-chevron-down";
    },
    tabArrowClass() {
      return this.tabShow ? "fa-chevron-up" : "fa-chevron-down";
    },
    fullTextResult() {
      return this.ctrl.tabActived === "sub"
        ? `${this.subSearchText}.${this.topSelectText}`
        : this.topSearchText;
    },
    domainSubPoperVisible() {
      return this.ctrl.tabActived === "sub" && this.submodel.popvisible;
    },
    ...Vuex.mapState({
      topAssets: (state) => {
        return state.dapp.rootassets;
      },
    }),
    topDomains() {
      let text = this.submodel.filterkey + "";
      return text
        ? this.topAssets.filter((r) => r.domaintext.startsWith(text))
        : this.topAssets;
    },
    topType() {
      if (!this.topSearchText) return "";
      let type = getDomainTopType(this.topSearchText);
      return this.$t(`g.${type}`);
    },
  },
  methods: {
    toSub() {
      const searchSubText = this.subSearchText;
      if (!searchSubText) {
        this.$message(this.$basTip.error(this.$t("l.DomainSearchInputTips")));
        return;
      }
      this.$router.push({
        path: `/apply/${this.topSelectText}/${this.subSearchText}`,
      });
    },
    toTop() {
      const searchTopText = this.topSearchText;
      if (!searchTopText) {
        this.$message(this.$basTip.error(this.$t("l.DomainSearchInputTips")));
        return;
      }
      this.$router.push({
        path: `/apply/${this.topSearchText}`,
      });
    },
    handleTabs() {
      this.tabShow = !this.tabShow;
    },
    closeDomainSubPoper() {
      this.submodel.popvisible = false;
    },
    togglePopTopSelect() {
      this.submodel.popvisible = !this.submodel.popvisible;
    },
    tabClick(tag) {
      console.log(tag);
      this.ctrl.tabActived = tag;
      this.ctrl.searchState = false;
      this.tabShow = !this.tabShow;
    },
    validCustomize() {
      const roottext = this.submodel.customize + "";
      if (roottext === "" || !roottext.trim().length) {
        this.submodel.isCustomizeRoot = false;
      }
      let msg = "";

      try {
        //100002,100001
        CheckLegalRoot(roottext);
        const web3State = this.$store.getters["web3State"];
      } catch (ex) {
        if (ex === 100001) {
          msg = this.$t("code.100001", { max: MAX_ROOTDOMAIN_LENGTH });
        } else {
          msg = this.$t(`code.${ex}`, { text: roottext });
        }

        this.$message(this.$basTip.error(msg));
        return;
      }
    },
    selectTopText(name) {
      this.topSelectText = name;
      this.submodel.popvisible = false;
    },
    async addCustomizeRoot() {
      const roottext = this.submodel.customize + "";
      if (roottext === "" || !roottext.trim().length) {
        this.submodel.isCustomizeRoot = false;
        return;
      }
      let msg = "";

      try {
        //100002,100001
        CheckLegalRoot(roottext);
        const web3State = this.$store.getters["web3State"];
        const chainId = web3State.chainId || getDefaultNetwork().chainId;

        const resp = await valid4CustomizeRoot(roottext, chainId);
        console.log(resp);
        const text = resp.domaintext;
        this.submodel.isCustomizeRoot = true;
        this.submodel.customize = text;
        this.selectTopText(text);
      } catch (ex) {
        console.log(ex);
        this.submodel.isCustomizeRoot = false;
        switch (ex) {
          case ROOT_REGIST_CLOSE:
            msg = this.$t(`code.${ROOT_REGIST_CLOSE}`, { text: roottext });
            console.log(msg);
            this.$message(this.$basTip.error(msg));
            return;
          case 100001:
            msg = this.$t("code.100001", { max: MAX_ROOTDOMAIN_LENGTH });
            this.$message(this.$basTip.error(msg));
            return;
          case 100002:
          case 200001:
            msg = this.$t(`code.${ex}`, { text: roottext });
            this.$message(this.$basTip.error(msg));
            return;
          default:
            //9999:网络异常,请重试
            msg = this.$t("code.9999");
            this.$message(this.$basTip.error(msg));
            console.error(ex);
            return;
        }
        return;
      }
    },
    async reloadRootAssets() {
      this.submodel.loading = true;
      await this.$store.dispatch("dapp/loadRootAssets");
      const assets = this.$store.state.dapp.rootassets;
      if (assets.length) {
        this.topSelectText = assets[0].domaintext;
      }
      this.submodel.loading = false;
    },
  },
  async beforeMount() {
    try {
      const assets = await checkStorage(ROOT_ASSETS);
      // console.log('assets',assets)
      if (assets && assets.length && this.topSelectText === ".") {
        console.log("fill default selectTopText", assets[0].domaintext);
        this.selectTopText(assets[0].domaintext);
      }
    } catch (ex) {}
    await this.$store.dispatch("dapp/fillRootAssets");

    await this.$store.dispatch("dapp/loadRootAssets");
    const assets = this.$store.state.dapp.rootassets;
    if (assets.length) {
      this.topSelectText = assets[0].domaintext;
    }
  },
  async mounted() {
    let ruleState = this.$store.getters["dapp/ruleState"];
    this.ruleState = Object.assign(this.ruleState, ruleState);

    console.log(">>DomainSearch Mounted>>>>");

    this.$store.dispatch("assets/syncLatestRootDomains");
